import * as CSS from "../../utils/css"
import { RenderTarget } from "../../types/RenderEnvironment"

/**
 * Add propagation-blocking CSS if we haven't already, and if we're not on the canvas. If
 * we add this while on the canvas, strange behaviour can appear in the Component panel, with the
 * drag event being blocked.
 */
export const injectFrameCSSRules = () => {
    const isCanvas = RenderTarget.current() === RenderTarget.canvas
    CSS.add(`
          ${!isCanvas ? ` [data-framer-component-type="Frame"] * { pointer-events: auto; } ` : ``}
          [data-framer-component-type="Frame"] {
              position: absolute;
          }
      `)
}
